*{
  box-sizing: border-box;
  /* outline: 1px solid red; */
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
ul{
  display: flex;
  padding: 0;
  margin: 10px 0;
}
ul li{
  list-style: none;
  flex-grow: 1;
}
.login{
  background: red;
}
.login,
.main{
  min-height: 100vh;
}
.main{
  background: blue;
}

/* //--------------------------regestration */
.containerRegestration{
  max-width: 500px;
  width: 100%;
  margin: 0 auto;
}

.containerRegestration input{
  display: flex;
  height: 30px;
  outline: none;
  padding-left: 10px;
}

.conteiner-form form{
  padding: 10px;
  border: 1px solid gray;
}
.conteiner-form form>div{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

#submit-registr{
  display: flex;
  margin-left: auto;
}
.conteiner-form form>div.error{
  display: flex;
  justify-content: flex-end;
  color: red;
  font-size: 12px;
  height: 12px;
  margin-top: -10px;
}
/* //--------------------------regestration */